<template>
  <div>
    <el-row :gutter="20">
      <el-col class="mb20">
        <el-card>
          <div style="margin-top: 20px;">
            <el-form :inline="true">
              <el-form-item label="IP">
                <el-input v-model="queryParams.ip" placeholder="IP"></el-input>
              </el-form-item>
              <el-form-item label="城市">
                <el-input v-model="queryParams.city" placeholder="城市"></el-input>
              </el-form-item>
              <el-form-item label="时间段">
                <el-date-picker
                  v-model="daterange"
                  type="daterange"
                  :clearable="false"
                  value-format="yyyy-MM-dd"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                ></el-date-picker>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="getVisits()">查询</el-button>
              </el-form-item>
            </el-form>
          </div>
          <div style="padding:20px;">
            <el-table
              :data="visits"
              style="margin-bottom:20px;"
              v-loading="tableLoading"
              element-loading-background="rgba(0, 0, 0, 0.3)"
              element-loading-text="数据正在加载中"
              element-loading-spinner="el-icon-loading"
            >
              <el-table-column label="时间" show-overflow-tooltip>
                <template slot-scope="scope">{{timeAgo(scope.row.createTime)}}</template>
              </el-table-column>
              <el-table-column prop="ip" label="IP"></el-table-column>
              <el-table-column prop="province" label="省"></el-table-column>
              <el-table-column prop="city" label="城市"></el-table-column>
              <el-table-column prop="isp" label="ISP"></el-table-column>
              <el-table-column prop="source" label="来源"></el-table-column>
              <el-table-column prop="url" label="URL" show-overflow-tooltip>
                <template slot-scope="scope">
                  <el-link
                    :underline="false"
                    :href="scope.row.url"
                    target="_blank"
                    type="primary"
                  >{{ scope.row.url }}</el-link>
                </template>
              </el-table-column>
              <el-table-column prop="browser" label="浏览器"></el-table-column>
              <el-table-column prop="os" label="系统"></el-table-column>
              <el-table-column prop="terminal" label="终端"></el-table-column>
              <el-table-column prop="type" label="类型"></el-table-column>
            </el-table>
            <pagination
              @sizeChange="handleSizeChange"
              @currentChange="handleCurrentChange"
              :pageNum="queryParams.pageNum"
              :pageSize="queryParams.pageSize"
              :total="total"
            ></pagination>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Pagination from "@/components/pagination";
import visitApi from "@/api/visitApi";
import commonUtil from "@/utils/commonUtil";
import moment from "moment";
export default {
  components: {
    Pagination
  },
  data() {
    return {
      visits: [],
      tableLoading: false,
      queryParams: {
        pageNum: 1,
        pageSize: 10
      },
      daterange: [],
      total: 0
    };
  },
  created() {
    let today = moment()
      .locale("zh-cn")
      .format("YYYY-MM-DD");
    this.daterange = [today, today];
    this.getVisits();
  },
  methods: {
    getVisits() {
      this.queryParams.startDate = this.daterange[0];
      this.queryParams.endDate = this.daterange[1];
      this.tableLoading = true;
      visitApi.page(this.queryParams).then(res => {
        this.tableLoading = false;
        this.visits = res.data.records;
        this.total = res.data.total;
      });
    },
    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.getVisits();
    },
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.getVisits();
    },
    timeAgo(time) {
      return commonUtil.timeAgo(time);
    }
  }
};
</script>

